import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import { BtnAddNew } from 'components/common/button';
import { Card, Row, Col } from 'antd';
import routeConstant from 'routes/route/routeConstant';
import TableDropoutList from './TableDropoutList';
import SearchBar from './SearchBar';
import './style.scss';

class DropoutListlPage extends Component {
  state = {};

  render() {
    return (
      <Card title="Xt hc tip">
        <SearchBar />
        <Row>
          <Col span={24}>
            <div className="btn-group">
              <BtnAddNew
                title="Nhp xt hc tip"
                href={routeConstant.XET_DUYET__NGUNG_THOI_HOC_HOC_TIEP__NHAP_DU_LIEU}
              />
            </div>
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <TableDropoutList />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <DropoutListlPage />
  </Page>
);



